import React from "react";
import qs from "query-string";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Select from "react-select";
import ReportItem from "./ReportItem";
import Button from "muicss/lib/react/button";
import Expire from "../common/Expire";
import Modal from "react-modal";
import { Navigation } from "../../lib";

export default class ReportsPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      brokersList: [],
      banksList : [],
      selectedBrokers: [],
      selectedBank: null,
      showOtherBrokersOptions: false,
      showBankList: false,
    };
  }

  UNSAFE_componentWillMount() {
    let brokersList = [{ value: 0, label: "All" }];
    this.props.membersList
      .filter(
        (m) => m.member_id !== parseInt(this.props.authData.member_id, 10)
      )
      .map((m) =>
        brokersList.push({
          value: m.member_id,
          label: m.full_name,
        })
      );
    let banksList = [];
    this.props.banksList.map((b) =>
      banksList.push({
        value: b.bank_id,
        label: b.bank_name,
      })
    );
    this.setState({ brokersList, banksList });
  }

  selectBrokersChange(selectedBrokers) {
    if (selectedBrokers.filter((s) => s.value === 0).length > 0) {
      this.setState({ selectedBrokers: this.state.brokersList.slice(1) });
    } else {
      this.setState({ selectedBrokers });
    }
  }

  selectBankChange(selectedBank) {
    this.setState({ selectedBank });
  }

  clickOtherBrokersReport() {
    this.setState({
      showOtherBrokersOptions: !this.state.showOtherBrokersOptions,
    });
  }

  render() {
    let parentReportItems = null;
    if (this.props.reports) {
      parentReportItems = this.props.reports.map((item, index) => (
        <ReportItem
          key={index}
          item={item}
          openReportAsPdf={this.props.getReportPDF}
        />
      ));
    }

    let toast = null;
    if (this.props.toast.show) {
      toast = (
        <Expire closeToast={this.props.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.toast.message}</p>
            <Button variant="flat" onClick={this.props.closeToast}>
              {this.props.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <div className="report-outer-container">
        {toast}
        <div className="reports-list reports-container">
          <Row>
            <p className="report-list-title">Standard Reports</p>
          </Row>
          <Row>
            <Col
              md="11"
              sm="11"
              xs="11"
              onClick={() =>
                Navigation.go(
                  `/reports/view-report?${qs.stringify({
                    report: "all-in-one",
                  })}`
                )
              }
            >
              <img
                src="/images/icons/reports.png"
                className="reports-row-icon"
                alt="report"
              />
              All in One Report
            </Col>
          </Row>
        </div>
        {this.props.authData.company_id !== 2 && this.props.authData.is_broker && (
          <div className="reports-list reports-container">
            <Row>
              <p className="report-list-title">MTM Goals Reports</p>
            </Row>
            <Row>
              <Col
                md="11"
                sm="11"
                xs="11"
                onClick={() =>
                  Navigation.go(
                    `/reports/view-report?${qs.stringify({
                      report: "mtm",
                      "mtm-data": parseInt(this.props.authData.member_id, 10),
                    })}`
                  )
                }
              >
                <img
                  src="/images/icons/reports.png"
                  className="reports-row-icon"
                  alt="report"
                />
                Personal MTM Goals Report
              </Col>
            </Row>
            {(this.props.authData.member_role === 2 ||
              this.props.authData.member_role === 3 ||
              this.props.authData.addons.includes(1) ||
              this.props.authData.addons.includes(32) ||
              this.props.authData.addons.includes(54)) && (
              <Row>
                <Col
                  md="11"
                  sm="11"
                  xs="11"
                  onClick={() => this.clickOtherBrokersReport()}
                >
                  <img
                    src="/images/icons/reports.png"
                    className="reports-row-icon"
                    alt="report"
                  />
                  Other Brokers' MTM Goals Report
                </Col>
                <div
                  className={
                    this.state.showOtherBrokersOptions
                      ? "select-brokers-container"
                      : "hidden"
                  }
                >
                  <Col md="11" sm="11" xs="11">
                    <Select
                      value={this.state.selectedBrokers}
                      onChange={(e) => this.selectBrokersChange(e)}
                      options={
                        this.state.selectedBrokers.length ===
                        this.state.brokersList.length - 1
                          ? []
                          : this.state.brokersList
                      }
                      inputValue={this.state.brokersSearchVal}
                      onInputChange={(e) =>
                        this.setState({ brokersSearchVal: e })
                      }
                      isMulti={true}
                      isSearchable={true}
                      placeholder="Select Brokers"
                    />
                    <div className="add-note-buttons">
                      <Button
                        variant="flat"
                        disabled={this.state.selectedBrokers.length === 0}
                        onClick={() =>
                          Navigation.go(
                            `/reports/view-report?${qs.stringify({
                              report: "mtm",
                              "mtm-data": this.state.selectedBrokers.map((b) =>
                                parseInt(b.value, 10)
                              ),
                              "mtm-combined": 0,
                            })}`
                          )
                        }
                      >
                        View Separate
                      </Button>
                      <Button
                        variant="flat"
                        disabled={this.state.selectedBrokers.length === 0}
                        onClick={() =>
                          Navigation.go(
                            `/reports/view-report?${qs.stringify({
                              report: "mtm",
                              "mtm-data": this.state.selectedBrokers.map((b) =>
                                parseInt(b.value, 10)
                              ),
                              "mtm-combined": 1,
                            })}`
                          )
                        }
                      >
                        View Combined
                      </Button>
                    </div>
                  </Col>
                </div>
              </Row>
            )}
            {(this.props.authData.member_role === 2 ||
              this.props.authData.member_role === 3) && (
              <Row
                className={
                  this.state.showOtherBrokersOptions
                    ? "other-brokers-opened"
                    : ""
                }
              >
                <Col
                  md="11"
                  sm="11"
                  xs="11"
                  onClick={() =>
                    Navigation.go(
                      `/reports/view-report?${qs.stringify({
                        report: "full-mtm",
                      })}`
                    )
                  }
                >
                  <img
                    src="/images/icons/reports.png"
                    className="reports-row-icon"
                    alt="report"
                  />
                  Full Blown MTM
                </Col>
              </Row>
            )}
          </div>
        )}

        {this.props.authData.company_id !== 2 &&
          this.props.authData.is_broker &&
          (this.props.authData.member_role === 2 ||
            this.props.authData.member_role === 3 ||
            this.props.authData.addons.includes(27) ||
            this.props.authData.addons.includes(32)) &&
          this.props.banksList.length > 0 && (
            <div className="reports-list reports-container">
              <Row>
                <p className="report-list-title">Bank Reports</p>
              </Row>
              <Row>
                <Col
                  md="11"
                  sm="11"
                  xs="11"
                  onClick={() => this.setState({ showBankList: !this.state.showBankList })}
                >
                  <img
                    src="/images/icons/reports.png"
                    className="reports-row-icon"
                    alt="report"
                  />
                  Bank Pipeline
                </Col>
                <div
                  className={
                    this.state.showBankList
                      ? "select-brokers-container"
                      : "hidden"
                  }
                >
                  <Col md="11" sm="11" xs="11">
                    <Select
                      value={this.state.selectedBank}
                      onChange={(e) => this.selectBankChange(e)}
                      options={this.state.banksList}
                      inputValue={this.state.banksSearchVal}
                      onInputChange={(e) =>
                        this.setState({ banksSearchVal: e })
                      }
                      isMulti={false}
                      isSearchable={true}
                      placeholder="Select Bank"
                    />
                    <div className="add-note-buttons">
                      <Button
                        variant="flat"
                        disabled={!this.state.selectedBank}
                        onClick={() =>
                          Navigation.go(
                            `/reports/view-report?${qs.stringify({
                              report: "bank-pipeline",
                              "bank-id": this.state.selectedBank.value,
                            })}`
                          )
                        }
                      >
                        View Report
                      </Button>
                    </div>
                  </Col>
                </div>
              </Row>
            </div>
          )}
        <div className="reports-list reports-container">
          <Row>
            <p className="report-list-title">Custom Reports</p>
          </Row>
          {parentReportItems}
        </div>

        {this.props.loadingReport && (
          <Modal
            className="loading-modal"
            ariaHideApp={false}
            contentLabel="modal"
            isOpen={true}
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}
