import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import Modal from "react-modal";
import ReportsPage from "./ReportsPage";
import * as reportsActions from "../../actions/reportsActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";

class ReportsContainer extends React.Component {
  async componentDidMount() {
    this.props.navActions.updateNavTitle("Reports");
    await this.getMembersList();
    await this.getBanksList();
    await this.props.actions.getReportList();
  }

  getMembersList() {
    let authData = JSON.parse(localStorage.getItem("data"));
    if (
      authData.member_role === 2 ||
      authData.member_role === 3 ||
      authData.addons.includes(1) ||
      authData.addons.includes(32) ||
      authData.addons.includes(54)
    ) {
      this.props.actions.getMembersList();
    }
  }

  getBanksList() {
    let authData = JSON.parse(localStorage.getItem("data"));
    if (
      authData.member_role === 2 ||
      authData.member_role === 3 ||
      authData.addons.includes(27) ||
      authData.addons.includes(32)
    ) {
      this.props.actions.getBanksList();
    }
  }

  render() {
    return (
      <div>
        {!this.props.reports.loadingReportsList ? (
          <ReportsPage
            loadingReport={this.props.reports.loadingReport}
            reports={this.props.reports.listReports}
            membersList={this.props.reports.members}
            banksList={this.props.reports.banks}
            toast={this.props.reports.toast}
            authData={JSON.parse(localStorage.getItem("data"))}
            getAllInOneReport={this.props.actions.getAllInOneReport}
            getReportPDF={this.props.actions.getReportPDF}
            getMTMReport={this.props.actions.getMTMReport}
            getFullBlownMTMReport={this.props.actions.getFullBlownMTMReport}
            getBankPipelineReport={this.props.actions.getBankPipelineReport}
            closeToast={this.props.actions.closeToast}
          />
        ) : (
          <Modal
            className="loading-modal"
            ariaHideApp={false}
            contentLabel="modal"
            isOpen={this.props.reports.loadingReportsList}
            shouldCloseOnOverlayClick={false}
          >
            <div
              className={
                this.props.reports.loadingReportsList
                  ? "centered-spinner"
                  : "hidden"
              }
            >
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    reports: state.reports,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(reportsActions, dispatch),
    navActions: bindActionCreators(navigationTitleActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ReportsContainer);
